<template>
    <div class="ManualTest">
        <div>
            <div style="font-size:20px;font-weight:bold">自动检测-时间设定</div>
            <div style="margin-top:20px;width:418px;">
                <el-input v-model="minutes" placeholder="请输入内容" style="width:320px"></el-input>
                <el-button type="primary" @click="submitTime">确定</el-button>
            </div>
        </div>
        <div style="border:1px solid #C0C4CC;padding:10px;border-radius:10px;margin-top:20px;width:400px">
            <div style="font-size:20px;font-weight:bold">手动检测</div>
            <div style="margin-top:20px">
                
                <el-form ref="form" :model="form" label-width="80px">
                    <el-form-item label="服务列表">
                        <el-select v-model="form.region" placeholder="请选择服务">
                        <el-option :label="item.sName" :value="item.sId" v-for="(item,index) in serviceList" :key="index"></el-option>
                        </el-select>
                    </el-form-item>
                </el-form>
                <el-button type="primary" @click="test" >测试</el-button>
                <el-button type="primary" @click="repair"  v-show="flag">修复</el-button>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name:'ManualTest',
    data(){
        return{
            minutes:0,
            serviceList:[],
            form:{
                region:''
            },
            fData:{},
            flag:false
        }
    },
    methods:{
        submitTime(){
            console.log(this.minutes)
            if (!isNaN(this.minutes)) {
                this.$axios.put('http://192.168.1.155:8888/scheduController',{'minutes':this.minutes}).then((msg)=>{
            
                        if (msg.data) {
                        this.$message({
                        message: '恭喜你，设置成功',
                        type: 'success'
                    });
                    }
                })
            }else{
                this.$message({
                    message: '请填写数字',
                    type: 'warning'
                });
            }
        },
        //测试
        test(){
            if (this.form.region) {
                for (let i = 0; i < this.serviceList.length; i++) {
                    if (this.serviceList[i].sId == this.form.region) {
                        this.fData = this.serviceList[i]
                    }
                }
                this.$axios.post('http://192.168.1.155:8888/serverController/testServer',this.fData).then((msg)=>{
                    console.log(msg)
                    if (msg.status == 200) {
                        this.$message({
                            message: msg.data[0],
                            duration:3000
                        })
                    }else{
                        this.$message.error('有错误，请及时修复');
                    }
                    this.form.region = ''
                })
            }else{
                this.$message.error('服务不能为空');
            }
        },
        //修复
        repair(){

        }
    },
    mounted(){
        //获取服务列表
        this.$axios.get('http://192.168.1.155:8888/serverController').then((msg)=>{
          this.serviceList = msg.data
        })
    }
}
</script>

<style scoped>
.ManualTest{
    width:1000px;
    margin: 20px auto
}
</style>


